import * as THREE from "three"
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import mapboxgl, { LngLatLike, LngLat, Map, CustomLayerInterface } from 'mapbox-gl';

class Fire {

    layerId: string
    size: number = 100
    // parameters to ensure the model is georeferenced correctly on the map
    modelOrigin: LngLatLike = [139.6947214379419, 35.679861724509934];
    modelAltitude = 0;
    modelRotate = [Math.PI / 2, 0, 0];

    // elevation
    // https://github.com/mapbox/mapbox-gl-js/issues/10318
    // elevation
    // https://github.com/mapbox/mapbox-gl-js/issues/10318
    modelAsMercatorCoordinate!: mapboxgl.MercatorCoordinate;
    modelTransform: any = {}

    public constructor(lngLat: LngLatLike, layerId: string, size: number) {
        this.layerId = layerId
        this.size = size
        this.setLngLat(lngLat)
    }

    public setLngLat(lngLat: LngLatLike) {
        this.modelAsMercatorCoordinate = mapboxgl.MercatorCoordinate.fromLngLat(
            lngLat,
            this.modelAltitude
        );

        this.modelTransform = {
            translateX: this.modelAsMercatorCoordinate.x,
            translateY: this.modelAsMercatorCoordinate.y,
            translateZ: this.modelAsMercatorCoordinate.z,
            rotateX: this.modelRotate[0],
            rotateY: this.modelRotate[1],
            rotateZ: this.modelRotate[2],
            /* Since our 3D model is in real world meters, a scale transform needs to be
             * applied since the CustomLayerInterface expects units in MercatorCoordinates.
             */
            scale: this.modelAsMercatorCoordinate.meterInMercatorCoordinateUnits() * this.size
        };
    }

    clock = new THREE.Clock();
    mixer?: THREE.AnimationMixer;
    camera?: THREE.Camera;
    scene?: THREE.Scene;
    renderer?: THREE.WebGLRenderer
    map: any;

    // transformation parameters to position, rotate and scale the 3D model onto the map

    // configuration of the custom layer for a 3D model per the CustomLayerInterface
    public charactorLayer(): CustomLayerInterface {
        return {
            id: this.layerId,
            type: 'custom',
            renderingMode: '3d',
            onAdd: (map: any, gl: WebGLRenderingContext) => {
                this.camera = new THREE.Camera();
                this.scene = new THREE.Scene();

                // create two three.js lights to illuminate the model
                var directionalLight = new THREE.DirectionalLight(0xffffff);
                directionalLight.position.set(0, -70, 100).normalize();
                this.scene.add(directionalLight);

                var directionalLight2 = new THREE.DirectionalLight(0xffffff);
                directionalLight2.position.set(0, 70, 100).normalize();
                this.scene.add(directionalLight2);

                // use the three.js GLTF loader to add the 3D model to the three.js scene
                var loader = new GLTFLoader();

                loader.load(
                    // 'https://cdn.glitch.com/378a8eca-f405-469a-b32e-b603069e5372%2Funtitled.glb?v=1584360698775',
                    './fire/scene.gltf',
                    (gltf: any) => {
                        const obj = gltf.scene;
                        const animations = gltf.animations;

                        if (animations && animations.length) {

                            //Animation Mixerインスタンスを生成
                            this.mixer = new THREE.AnimationMixer(obj);

                            //全てのAnimation Clipに対して
                            for (let i = 0; i < animations.length; i++) {
                                let animation = animations[i];

                                //Animation Actionを生成
                                let action = this.mixer.clipAction(animation);

                                //ループ設定（1回のみ）
                                // action.setLoop(THREE.LoopOnce);

                                //アニメーションの最後のフレームでアニメーションが終了
                                // action.clampWhenFinished = true;
                                console.log('play');
                                //アニメーションを再生
                                action.play();
                            }
                        }
                        this.scene?.add(gltf.scene);
                    }// .bind(this)
                );
                this.map = map
                this.renderer = new THREE.WebGLRenderer({
                    canvas: map.getCanvas(),
                    context: gl,
                    antialias: true
                })
                this.renderer.autoClear = false;
            },
            render: (gl: WebGLRenderingContext, matrix: any) => {
                // requestAnimationFrame(() => { this.render(); });
                var rotationX = new THREE.Matrix4().makeRotationAxis(
                    new THREE.Vector3(1, 0, 0),
                    this.modelTransform.rotateX
                );
                var rotationY = new THREE.Matrix4().makeRotationAxis(
                    new THREE.Vector3(0, 1, 0),
                    this.modelTransform.rotateY
                );
                var rotationZ = new THREE.Matrix4().makeRotationAxis(
                    new THREE.Vector3(0, 0, 1),
                    this.modelTransform.rotateZ
                );

                var m = new THREE.Matrix4().fromArray(matrix);

                // console.log(map.painter.terrain.getAtPoint(modelAsMercatorCoordinate) * modelTransform.scale * 100)
                var l = new THREE.Matrix4()
                    .makeTranslation(
                        this.modelTransform.translateX,
                        this.modelTransform.translateY,
                        this.modelTransform.translateZ?? 0
                        // this.map.painter.terrain.getAtPoint(this.modelAsMercatorCoordinate) * this.modelTransform.scale / this.size
                    )
                    .scale(
                        new THREE.Vector3(
                            this.modelTransform.scale,
                            - this.modelTransform.scale,
                            this.modelTransform.scale
                        )
                    )
                    .multiply(rotationX)
                    .multiply(rotationY)
                    .multiply(rotationZ);

                if (this.mixer) {
                    this.mixer.update(this.clock.getDelta());
                }
                if (this.camera) {
                    this.camera.projectionMatrix = m.multiply(l);
                }
                this.renderer?.state.reset();
                this.renderer?.render(this.scene!, this.camera!);
                this.map.triggerRepaint();
            }
        }
    }
}

export default Fire